import $               from 'ZEPTO_PATH';
import header          from 'MODULES_PATH/header.js';
import layout          from 'MODULES_PATH/layout';
import navSlider       from 'MODULES_PATH/navSlider';
import FooterAd        from 'MODULES_PATH/footerAd';
import goTop           from 'MODULES_PATH/goTop';
import LazyLoad        from 'MODULES_PATH/indexLazyLoad';
import Timer           from 'UNIT_PATH/timer';
import remind          from 'UNIT_PATH/remind';
import Notify          from 'MODULES_PATH/notify';
import Model           from 'UNIT_PATH/model';

window.$ = $;

// 页面布局调整
layout();

// 公共头部
header.headerInit();

// 回到顶部按钮
goTop();

// 懒加载
LazyLoad();

// 回到顶部按钮
goTop();

if($('.actwilling-timer').length) {
	// 取活动预告状态
	Notify.getNotifyStatus();

	// 开售提醒
	Notify.notifyEventBind();
}

// 尾部广告
var footerAd = new FooterAd();

footerAd.init();

// 导航滑动
navSlider.init();

	
// 倒计时
if($('.red-actlist a').length || $('.notify-wrap .notify-item').length) {
	Model.getActBeLeftTime(function(data) {
		if(data.dataTime <= 0) return;

		$('#timer-box').attr('data-time', data.dataTime);

		var timer = new Timer({
			timerBox: $('#timer-box'),
			timeUp: function() {

			}
		});

		timer._run();
	});
}


